<template>
  <div class="chatbot-container" @click="closeMenuOnOutsideClick" ref="chatbotContainer">
    <div class="overlay-menu" :class="{ 'active': isMenuOpen }" @click.stop>
      <button class="close-menu" @click="toggleMenu">X</button>
      <button class="new-chat-button" @click="startNewChat">New Chat</button>
      <div class="chat-history">
        <h3>Chat History</h3>
        <ul>
          <li v-for="chat in chatHistory" 
              :key="chat._id" 
              @click="loadChat(chat)"
              class="chat-history-item">
            {{ formatChatHistory(chat) }}
          </li>
        </ul>
      </div>
      <div class="menu-footer">
        <div class="user-info">
          <div class="user-avatar">{{ userInitials }}</div>
          <div class="user-name">{{ userName }}</div>
        </div>
        <div class="settings-container">
          <button class="settings-button" @click.stop="toggleSettingsMenu">
            <font-awesome-icon icon="cog" />
          </button>
          <div v-if="isSettingsMenuOpen" 
              class="settings-popup" 
              ref="settingsPopup">
            <div class="popup-arrow"></div>
            <ul>
              <li><button @click="goToAccountSettings">Account Settings</button></li>
              <li><button @click="goToHelpAndSupport">Help & Support</button></li>
              <li><button @click="logout">Logout</button></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="chat-layout">
      <div class="header">
        <div class="header-left">
          <button class="menu-button" @click="toggleMenu"><font-awesome-icon icon="bars" /></button>
        </div>
        <div class="header-center">
          <h2>[의상현] DoctorChat</h2>
        </div>
        <div class="header-right"></div>
      </div>

      <div class="chat-main" ref="chatMain">
        <div class="chat-window" ref="chatWindow">
          <div v-if="!currentChat" class="start-chat">
            <div class="start-chat-content">
              <div class="circle"></div>
              <p>Hello! Nice to see you here!</p>
              <p>Please press the "Start chat" button to begin.</p>
              <button @click="startNewChat" class="start-chat-button">Start chat</button>
            </div>
          </div>
          <div v-else class="chat-messages" ref="chatMessages">
            <div v-for="message in currentChat.chat_list" :key="message.date" class="message_container">
              <div :class="['message-header', message.sender]">
                <span v-if="message.sender !== 'user'" class="message-icon"></span>
                <span class="message-name">{{ message.sender === 'user' ? userName : message.sender === 'bot' ? 'DoctorBot' : 'SpecialistBot' }}</span>
                <span v-if="message.sender === 'user'" class="message-time">{{ formatTime(message.date) }}</span>
              </div>
              <div :class="['message', message.sender]">
                <template v-if="message.sender === 'user'">
                  {{ message.message }}
                </template>
                <div v-else v-html="renderMarkdown(message.message)"></div>
              </div>
            </div>
            <div v-if="isLoading" class="message bot">
              <div class="message-content loading">
                <img :src="require('@/assets/loading.gif')" alt="Loading" />
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="footer-container" ref="footerContainer">
        <div v-if="currentChat" class="input-container">
          <div class="select-container">
            <div class="help-icon" @click.stop="toggleHelpTooltip" ref="helpIcon">
              <font-awesome-icon icon="question-circle" />
              <div class="tooltip" v-if="showHelpTooltip">
                - Normal: 일반적인 대화 모드입니다.<br>
                - Specialists: 전문의 상담 모드입니다.<br>
                더 많은 도움말을 확인하시려면 <br>메뉴의 Help & Support 를 확인하세요.
              </div>
            </div>
            <select v-model="selectedMode">
              <option value="normal">Normal</option>
              <option value="specialists">Specialists</option>
            </select>
          </div>
          <input 
            type="text" 
            v-model="newMessage" 
            @keyup.enter="sendMessage" 
            placeholder="Write a message and Wait One Minute for AI to Answer." 
            :disabled="isLoading"
          />
          <button @click="sendMessage" class="btn-send" :disabled="isLoading">
            <font-awesome-icon icon="paper-plane" />
          </button>
        </div>

        <footer class="footer">
          <div>DoctorChatbot V3.</div>
        </footer>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted, nextTick } from 'vue';
import { useRouter } from 'vue-router';
import { auth } from '@/firebase';
import { onAuthStateChanged } from "firebase/auth";
import axios from 'axios';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faBars, faPaperclip, faPaperPlane, faCog, faQuestionCircle } from '@fortawesome/free-solid-svg-icons';
import config from '@/config/config';
import { onClickOutside } from '@vueuse/core';
import MarkdownIt from 'markdown-it'


library.add(faBars, faPaperclip, faPaperPlane, faCog, faQuestionCircle);

export default {
  setup() {
    const md = new MarkdownIt();

    const renderMarkdown = (content) => {
      return md.render(content);
    };
    const selectedMode = ref('normal');
    const showHelpTooltip = ref(false);
    const helpIcon = ref(null);
    const router = useRouter();
    const isChatStarted = ref(false);
    const isLoading = ref(false);
    const userName = ref('');
    const userEmail = ref('');
    const isMenuOpen = ref(false);
    const isSettingsMenuOpen = ref(false);
    const settingsPopup = ref(null);
    const currentChat = ref(null);
    const chatHistory = ref([]);
    const newMessage = ref('');
    const isAuthenticated = ref(false);
    const chatStartDate = ref(null);
    const chatbotContainer = ref(null);
    const chatMain = ref(null);
    const chatWindow = ref(null);
    const footerContainer = ref(null);

    const toggleHelpTooltip = (event) => {
      event.stopPropagation();
      showHelpTooltip.value = !showHelpTooltip.value;
    };

    const closeHelpTooltip = (event) => {
      if (helpIcon.value && !helpIcon.value.contains(event.target)) {
        showHelpTooltip.value = false;
      }
    };

    const updateContainerHeight = () => {
      if (chatbotContainer.value && chatMain.value) {
        const windowHeight = window.visualViewport ? window.visualViewport.height : window.innerHeight;
        const headerHeight = window.innerWidth <= 768 ? 30 : 40;
        const footerHeight = 60; // 입력 컨테이너의 높이

        chatbotContainer.value.style.height = `${windowHeight}px`;
        
        const chatMainHeight = windowHeight - headerHeight - footerHeight;
        chatMain.value.style.height = `${chatMainHeight}px`;
        
        scrollToBottom();
      }
    };

    const userInitials = computed(() => userName.value.charAt(0).toUpperCase() || '');

    let unsubscribe;

    onMounted(() => {
      updateContainerHeight();
      window.addEventListener('resize', updateContainerHeight);
      document.addEventListener('click', closeHelpTooltip);

      
      if (window.visualViewport) {
        window.visualViewport.addEventListener('resize', updateContainerHeight);
        window.visualViewport.addEventListener('scroll', updateContainerHeight);
      }
      onClickOutside(settingsPopup, closeSettingsMenu);

      unsubscribe = onAuthStateChanged(auth, async (user) => {
        if (user) {
          isAuthenticated.value = true;
          userName.value = user.displayName || 'User';
          userEmail.value = user.email;
          
          try {
            const response = await axios.get(`${config.apiBaseUrl}/api/check-license`, {
              params: { email: user.email }
            });
            if (!response.data.license_number) {
              router.push('/account-settings');
            } else {
              await loadChatHistory();
            }
          } catch (error) {
            console.error("Error checking license:", error);
          }
        } else {
          isAuthenticated.value = false;
          router.push('/login');
        }
      });
    });
    
    onUnmounted(() => {
      document.removeEventListener('click', closeHelpTooltip);
      document.removeEventListener('click', closeMenuOnOutsideClick);
      window.removeEventListener('resize', updateContainerHeight);
      if (window.visualViewport) {
        window.visualViewport.removeEventListener('resize', updateContainerHeight);
        window.visualViewport.removeEventListener('scroll', updateContainerHeight);
      }
      if (unsubscribe) unsubscribe();
    });

    const toggleMenu = () => {
      isMenuOpen.value = !isMenuOpen.value;
      if (isMenuOpen.value) {
        loadChatHistory();
      }
    };

    const closeMenuOnOutsideClick = (event) => {
      if (isSettingsMenuOpen.value && 
          !event.target.closest('.settings-popup') && 
          !event.target.closest('.settings-button')) {
        isSettingsMenuOpen.value = false;
      }

      if (isMenuOpen.value && !event.target.closest('.overlay-menu') && !event.target.closest('.menu-button')) {
        isMenuOpen.value = false;
      }
    };

    const goToAccountSettings = () => {
      router.push('/account-settings');
      isSettingsMenuOpen.value = false;
      isMenuOpen.value = false;
    };

    const goToHelpAndSupport = () => {
      router.push('/help-support');
      isSettingsMenuOpen.value = false;
      isMenuOpen.value = false;

    }

    const toggleSettingsMenu = (event) => {
      event.stopPropagation();
      isSettingsMenuOpen.value = !isSettingsMenuOpen.value;
    };

    const closeSettingsMenu = () => {
      isSettingsMenuOpen.value = false;
    };

    const sendMessage = async () => {
      if (!isAuthenticated.value || newMessage.value.trim() === '' || isLoading.value) {
        return;
      }

      if (!chatStartDate.value) {
        chatStartDate.value = new Date().toISOString();
      }
      let messageToSend = newMessage.value.trim();
      if (selectedMode.value === 'specialists') {
        messageToSend = "consult specialists. " + messageToSend;
      }
      console.log(messageToSend);
      const userMessage_display = {
        sender: 'user',
        date: new Date().toISOString(),
        message: newMessage.value.trim()
      };

      const userMessage = {
        sender: 'user',
        date: new Date().toISOString(),
        message: messageToSend
      };
      
      console.log(userMessage);

      if (!currentChat.value) {
        currentChat.value = {
          chat_list: []
        };
      }
      
      currentChat.value.chat_list.push(userMessage_display);
      newMessage.value = '';
      isLoading.value = true;
      scrollToBottom();


      const result_message = [];
      try {
        const response = await axios.post(`${config.apiAIBaseUrl}`, {
          message_user: messageToSend,
          session_id: userEmail.value,
        });
        result_message.push(userMessage);
        const response_date = new Date().toISOString()
        const botMessage = {
          sender: 'bot',
          date: response_date,
          message: response.data.message_asst
        };
        result_message.push(botMessage);
        isLoading.value = false;
        currentChat.value.chat_list.push(botMessage);
        for (const message_sp of response.data.message_specialty_list)
        {
          currentChat.value.chat_list.push({
            sender: 'special',
            date: response_date,
            message: message_sp
          })
          result_message.push({
            sender: 'special',
            date: response_date,
            message: message_sp
          })
        }
        console.log(response.data.message_debug);
        const output_token = response.data.message_debug.match(/output=(\d+)/);
        const input_token = response.data.message_debug.match(/input=(\d+)/);
        const output = output_token ? parseInt(output_token[1], 10) : null;
        const input = input_token ? parseInt(input_token[1], 10) : null;

        await saveChatToServer(result_message, input, output);

      } catch (error) {
        isLoading.value = false;
        console.error(error);
        
        const errMessage = {
          sender: 'bot',
          date: new Date().toISOString(),
          message: "응답 처리 중 오류가 발생했습니다. 다시 시도해주세요."
        };
        currentChat.value.chat_list.push(errMessage);
        const botMessage = {
          sender: 'bot',
          date: new Date().toISOString(),
          message: error
        };
        result_message.push(userMessage,botMessage);
        await saveChatToServer(result_message, 0, 0);
      } finally {
        scrollToBottom();
      }
    };

    const saveChatToServer = async (chat_list, input_token, output_token) => {
      console.log(chat_list, input_token, output_token);
      try {
        await axios.put(`${config.apiBaseUrl}/api/update-chat`, {
          email: userEmail.value,
          chat_date: chatStartDate.value,
          chat_list: chat_list,
          input_token : input_token,
          output_token: output_token
        });
      } catch (error) {
        console.error("Error saving chat to server:", error);
      }
    };

    const startNewChat = () => {
      if (!isAuthenticated.value) {
        router.push('/login');
        return;
      }
      currentChat.value = {
        chat_list: []
      };
      chatStartDate.value = null;
      isMenuOpen.value = false;
      scrollToBottom();
    };

    const loadChatHistory = async () => {
      if (!isAuthenticated.value) return;
      try {
        isLoading.value = true;
        const response = await axios.get(`${config.apiBaseUrl}/api/chat-history`, {
          params: { email: userEmail.value }
        });
        chatHistory.value = response.data;
      } catch (error) {
        console.error("Error loading chat history:", error);
      } finally {
        isLoading.value = false;
      }
    };

    const loadChat = (chat) => {
      chatStartDate.value = chat.chat_date;
      currentChat.value = chat;
      isChatStarted.value = true;
      isMenuOpen.value = false;
      scrollToBottom();
    };

    const logout = async () => {
      try {
        await auth.signOut();
        isAuthenticated.value = false;
        userName.value = '';
        userEmail.value = '';
        isSettingsMenuOpen.value = false;
        isChatStarted.value = false;
        currentChat.value = null;
        router.push('/login');
      } catch (error) {
        console.error("Logout error:", error);
      }
    };

    const scrollToBottom = () => {
      nextTick(() => {
        const element = document.querySelector('.chat-messages');
        if (element) {
          element.scrollTop = element.scrollHeight;
        }
      });
    };

    const formatChatHistory = (chat) => {
      const firstMessage = chat.chat_list[0]?.message || 'No messages';
      return `${new Date(chat.chat_date).toLocaleDateString()} - ${firstMessage.substring(0, 30)}...`;
    }

    const formatTime = (dateString) => {
      return new Date(dateString).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    };

    return {
      isChatStarted,
      isLoading,
      userName,
      userEmail,
      isMenuOpen,
      isSettingsMenuOpen,
      currentChat,
      chatHistory,
      newMessage,
      isAuthenticated,
      userInitials,
      toggleMenu,
      toggleSettingsMenu,
      startNewChat,
      sendMessage,
      loadChatHistory,
      formatChatHistory,
      loadChat,
      logout,
      scrollToBottom,
      closeMenuOnOutsideClick,
      goToAccountSettings,
      goToHelpAndSupport,
      formatTime,
      chatbotContainer,
      chatWindow,
      chatMain,
      footerContainer,
      updateContainerHeight,
      closeSettingsMenu,
      settingsPopup,
      renderMarkdown,
      selectedMode,
      showHelpTooltip,
      toggleHelpTooltip,
      helpIcon,
    };
  }
};
</script>

